import React, { Component } from "react";
import "./dashboard.css";
class Dashboard extends Component {
  state = {};
  render() {
    return (
      <div className='main-dash'>
        <div className='text-center position-relative fe-title'>
          <img className='fe-dash' src={require("../../assets/images/Dashboard.png")} alt='' />
          <h4>Dashboard</h4>
          <div className='down-div'>
            <p className='text-center proven-p'>
              Manage your Turbo Preme license in our easy to use Dashboard!
            </p>
          </div>
          <div className='row versa'>
            <div className='col-md-5 '>
              <div className='easy-dash'>
                <h4>Easy Access to Check Task Activity</h4>
              </div>
            </div>
            
            {/* right-dashboard img */}
            <div className='col-md-7 dash-right text-right'>
              <picture>
                <source
                  media='(max-width: 575.98px)'
                  srcSet={require("../../assets/images/right-access-mobile.png")}
                />
                <img
                  className='img-dash'
                  src={require("../../assets/images/right-access.png")}
                  alt=''
                />
              </picture>
            </div>
            {/* left-dashboard img */}
            <div className='col-md-5  text-left no-bots'>
              <picture>
                <source
                  media='(max-width: 575.98px)'
                  srcSet={require("../../assets/images/mobile-dash-board.png")}
                />
                <img
                  className='img-dash'
                  src={require("../../assets/images/left-dashboard.png")}
                  alt=''
                />
              </picture>
            </div>
            <div className='col-md-5 bots '>
              <div className='easy-dash'>
                <h4>Easily download the lastest version of Turbo Preme all on our dashboard</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Dashboard;
