import React, { Component } from "react";
import "./faq.css";
import FaqSlider from "../FAQ-slider/faq-slider";
class FAQ extends Component {
  state = {};
  render() {
    return (
      <div className=' faq '>
        <div className='text-center position-relative fe-title'>
          <div className="">
            <div>
              <img className='fe-bg' src={require("../../assets/images/FAQ.png")} alt='' />
              <h4>Frequently Asked Questions</h4>
            </div>
            <div>
              <p className='text-center proven-p'>
                With over 160.000 successful chekouts. Turbo Preme is <br className="ops" /> the most successful
                sneaker bot on the market
              </p>
            </div>
            <div className=''>
              <FaqSlider />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default FAQ;
