import React, { Component } from "react";
import "./os.css";
class OpSystem extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className='row os'>
          {/* left side img */}
          <div className='col-md-4 left-os'>
            <img className='w-100' src={require("../../assets/images/left-window.png")} alt='' />
          </div>
          <div className='col-md-4 center-os text-center'>
            <h2>Working on Both!</h2>
            <p>
              We want to make sure all of our users are able to use our products; this is why we
              have made Turbo Preme working on Mac OS &amp; Windows OS.
            </p>
            <div className='control-btn'>
              <img className='w-os' src={require("../../assets/images/windows.png")} alt='' />
              <img src={require("../../assets/images/mac.png")} alt='' />
            </div>
          </div>
          {/* right side  */}
          <div className='col-md-4 right-os text-right'>
            <img className='w-100' src={require("../../assets/images/right-window.png")} alt='' />
          </div>
        </div>
      </div>
    );
  }
}

export default OpSystem;
