import React, { Component } from "react";
import Navbar from "../navbar/navbar";
import "./header.css";
import OpSystem from "./../operating-system/os";
class Header extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className='header'>
          <Navbar />
          <div className='saver'>
            <div className='logo text-center'>
              <img src={require("../../assets/images/logo.png")} alt='' />
            </div>
            <div className='text-center h-info position-relative'>
              <picture>
                <source
                  media='(max-width: 991.98px)'
                  srcSet={require("../../assets/images/small-turbo-header.png")}
                />
                <img
                  className='turbo-bg'
                  src={require("../../assets/images/turbo-header.png")}
                  alt=''
                />
              </picture>
              {/* description  */}
              <h3 className='font-weight-bold h3-des'>The fastest Supreme bot in the market.</h3>
              {/* main header  */}
              <h1 className='font-weight-bold h1-title'>We Really Turbo Speed</h1>
            </div>
          </div>
          <div className='text-center '>
            <button className='btn text-white h-btn'>BUY NOW</button>
          </div>
          <div>
            <OpSystem />
          </div>
        </div>
        {/* <!-- Modal --> */}
        <div
          className='modal fade'
          id='exampleModalCenter'
          tabIndex='-1'
          role='dialog'
          aria-labelledby='exampleModalCenterTitle'
          aria-hidden='true'>
          <div className='modal-dialog modal-dialog-centered' role='document'>
            <div className=' contact-c modal-content '>
              <div className='text-center position-relative fe-title'>
                <img
                  className='fe-bg'
                  src={require("../../assets/images/Contact-title.png")}
                  alt=''
                />
                {/* title contact us  */}
                <h4>Contact Us</h4>
                <div className='my-form'>
                  <form className='text-white'>
                    <div className='form-group text-left'>
                      <label htmlFor='Email'>Email Address</label>
                      <input
                        type='email'
                        id='Email'
                        className='form-control bg-transparent'
                        placeholder='your email address'
                      />
                    </div>
                    <div className='form-group text-left'>
                      <label htmlFor='Subject'>Subject</label>
                      <input
                        id='Subject'
                        type='text'
                        className='form-control bg-transparent'
                        placeholder='Your subject'
                      />
                    </div>
                    <div className='form-group text-left'>
                      <label htmlFor='textMsg'>Message</label>
                      <textarea
                        id='textMsg'
                        className='form-control bg-transparent'
                        cols='20'
                        placeholder='Type message'
                        rows='7'></textarea>
                    </div>
                    <div className='text-center'>
                      <button className='btn text-white h-btn'>Send</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
