import React, { Component } from "react";
import "./renew.css";
class Renew extends Component {
  state = {};
  render() {
    return (
      <div className='renew'>
        <div className='row versa '>
          <div className='col-md-7 dash-right text-left bg-pad'>
            <div className='easy-dash '>
              <h4>
                Renew your <br className="ops" /> membership with ease
              </h4>
            </div>
          </div>
          {/* right side  img */}
          <div className='col-md-5 pr-0 '>
            <div className='text-right'>
              <picture>
                <source media="(max-width: 575.98px)" srcSet={require("../../assets/images/mobile-renew.png")} />
                <img
                  className='card-pay'
                  src={require("../../assets/images/right-renew.png")}
                  alt=''
                />
              </picture>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Renew;
