import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./feature-slider.css";
class SimpleSlider extends React.Component {
  render() {
    var settings = {
      infinite: true,
      autoplay: true,
      autoplaySpeed: 7000,
      speed: 500,
      slidesToShow: 4,
      breakpoint: 768,
      responsive: [
        {
          breakpoint: 1199.98,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 991.98,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 570,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      <div className='f-slider'>
        <Slider {...settings}>
          <div>
            <div className='card'>
              <img src={require("../../assets/images/social-media.png")} alt='...' />
              <div className='position-relative card-body'>
                <h5 className='card-title text-white'>Worldwide Support</h5> 
                <p className='card-text'>
                  Turbo Preme Supports every Supreme region. This includes the United States,
                  Europe, and Japan.
                </p>
                <a href='link' className=''>
                  Learn more &rarr;
                </a>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img src={require("../../assets/images/secure-payment.png")} alt='...' />
              <div className='position-relative card-body'>
                <h5 className='card-title text-white'>Unlimited Tasks</h5> 
                <p className='card-text'>
                  Run as many tasks as you desire. Turbo Preme does not limit the amount of tasks a
                  user can run!
                </p>
                <a href='link' className=''>
                  Learn more &rarr;
                </a>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img src={require("../../assets/images/market.png")} alt='...' />
              <div className='position-relative card-body'>
                <h5 className='card-title text-white'>Lightweight</h5> 
                <p className='card-text'>
                  With Turbo Preme being tested for several months we have managed to make the bot
                  lightweight so you can just run the bot in the background with ease!
                </p>
                <a href='link' className=''>
                  Learn more &rarr;
                </a>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img src={require("../../assets/images/cash-register-machine.png")} alt='...' />
              <div className='position-relative card-body'>
                <h5 className='card-title text-white'>Proven Success</h5> 
                <p className='card-text'>
                  With a limited amount of releases &amp; users, Turbo has made 100's of checkouts
                  proving it is one of the best supreme bots in the market
                </p>
                <a href='link' className=''>
                  Learn more &rarr;
                </a>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img src={require("../../assets/images/infinity.png")} alt='...' />
              <div className='position-relative card-body'>
                <h5 className='card-title text-white'>Turbo Speed</h5> 
                <p className='card-text'>
                  Turbo Preme is the quickest bot in the market! We really are Turbo Speed!
                </p>
                <a href='link' className=''>
                  Learn more &rarr;
                </a>
              </div>
            </div>
          </div>
        </Slider>
      </div>
    );
  }
}

export default SimpleSlider;
