import React, { Component } from "react";

import "./proven.css";
class Proven extends Component {
  state = {};
  render() {
    return (
      <div className='pro'>
        <div className='text-center position-relative fe-title'>
          <img className='fe-bg p-bg' src={require("../../assets/images/Success.png")} alt='' />
          <h4>Proven Success</h4>
          <div>
            <p className='text-center proven-p'>
            With a limited amount of releases &amp; users, Turbo has made 100's of checkouts proving it is one of the best supreme bots in the market.
            </p>
          </div>
          <div className='proven-img text-center'>
            <img src={require("../../assets/images/proven_percentage.png")} alt='' />
          </div>
        </div>
      </div>
    );
  }
}

export default Proven;
