import React, { Component } from "react";
import Slider from "react-slick";
import "./faq-slider.css";
class FaqSlider extends Component {
  state = {};
  render() {
    var settings = {
      dots: true,
      autoplay: true,
      autoplaySpeed: 7000,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      centerMode: true,
      responsive: [
        {
          breakpoint: 1199.98,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 991.98,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 575.98,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      <div className='question'>
        <Slider {...settings}>
          <div>
            <div className='card'>
              <img
                className='question-img'
                src={require("../../assets/images/question.png")}
                alt=''
              />
              <div className='position-relative card-body'>
                <h5 className='card-title text-left text-white'>
                  What operating system are supported?
                </h5>
                <p className='card-text text-right'>Turbo Network supports Windows and Mac</p>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img
                className='question-img'
                src={require("../../assets/images/question.png")}
                alt=''
              />
              <div className='position-relative card-body'>
                <h5 className='card-title text-left text-white'>How much does Turbo Network Cost?</h5>
                <p className='card-text text-right'>To be Decided!</p>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img
                className='question-img'
                src={require("../../assets/images/question.png")}
                alt=''
              />
              <div className='position-relative card-body'>
                <h5 className='card-title text-left text-white'>When is the next restock?</h5>
                <p className='card-text text-right'>
                   We restock at random times. We recommend that you follow our <a className='t-link' href='https://twitter.com/TurboPreme' target='_blank' rel="noopener noreferrer"> Twitter   </a>  to be notified when we release.
                </p>
              </div>
            </div>
          </div>

          <div>
            <div className='card'>
              <img
                className='question-img'
                src={require("../../assets/images/question.png")}
                alt=''
              />
              <div className='position-relative card-body'>
                <h5 className='card-title text-left text-white'>Can I unbind/deactivate my key?</h5>
                <p className='card-text text-right'>
                  With the help of the dashboard every user can unbind and deactivate there key with
                  a click of a button.
                </p>
              </div>
            </div>
          </div>
          <div>
            <div className='card'>
              <img
                className='question-img'
                src={require("../../assets/images/question.png")}
                alt=''
              />
              <div className='position-relative card-body'>
                <h5 className='card-title text-left text-white'>
                  Does Turbo Network come with support?
                </h5>
                <p className='card-text text-right'>
                  Users can contact us on our{" "}
                  <a className='t-link' href='https://twitter.com/TurboPreme' target='_blank' rel="noopener noreferrer">
                    Twitter
                  </a>{" "}
                  and we also recommend you to join our discord server where we provide 24/7 support
                  with the help of staff who will guide you every step of the way! Note: Discord
                  server access is given on when purchasing Turbo Network.
                </p>
              </div>
            </div>
          </div>
        </Slider>
      </div>
    );
  }
}

export default FaqSlider;
