import React, { Component } from "react";
import "./navbar.css";
class Navbar extends Component {
  state = {
    navBg: false
  };
  handelNav = () => {
    this.setState({ 
      sideWidth: "250px"
    });
  };
  handelClose = () => {
    this.setState({
      sideWidth: "0"
    });
  };
  componentDidMount() {
    window.addEventListener("scroll", () => {
      if (window.innerWidth < 991.98) {
        this.setState({
          navPo: " fixed-top "
        });
      } else {
        this.setState({
          navPo: "  "
        });
      }
    });
  }
  render() {
    return (
      <div>
          {/* add class fixed to navbar */}
        <nav className={"navbar navbar-expand-lg navbar-light   " + this.state.navPo}>
          <button
            className='navbar-toggler'
            type='button'
            id='navBTN'
            onClick={this.handelNav}
            aria-label='Toggle navigation'>
            <img src={require("../../assets/images/icon-nav.png")} alt='' />
          </button>
          {/* navbar content  */}
          <div className='collapse navbar-collapse' id='navbarSupportedContent'>
            <ul className='navbar-nav mx-auto'>
              <li className='nav-item'>
                <a className='nav-link' href='#home'>
                  Home
                </a>
              </li>
              <li className='nav-item'>
                <a className='nav-link' href='#support'>
                  Support
                </a>
              </li>
              <li className='nav-item'>
                <a className='nav-link' href='#faq'>
                  FAQs
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link'
                  href='#contact'
                  data-toggle='modal'
                  data-target='#exampleModalCenter' >
                  Contact
                </a>
              </li>
              <li className='nav-item'>
                <a className='nav-link dash-im' href='#dashboard'>
                  Dashboard
                </a>
              </li>
            </ul>
          </div>
        </nav>

        <div id='mySidenav' className='sidenav' style={{ width: this.state.sideWidth }}>
          <span className='closebtn' onClick={this.handelClose}>
            &times;
          </span>
          {/* navbar in small screen  */}
          <nav className={"navbar navbar-expand-lg navbar-light " + this.state.navColor}>
            <div className='' id='navbarSupportedContent'>
              <ul className='navbar-nav mx-auto'>
                <li className='nav-item'>
                  <a className='nav-link' href='#home'>
                    Home
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#support'>
                    Support
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#faq'>
                    FAQs
                  </a>
                </li>
                <li className='nav-item'>
                  <a
                    className='nav-link'
                    href='#contact'
                    data-toggle='modal'
                    data-target='#exampleModalCenter' >
                    Contact
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link dash-im' href='#dashboard'>
                    Dashboard
                  </a>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}

export default Navbar;
