import React, { Component } from "react";
import "./features.css";
import SimpleSlider from "./../feature-slider/feature-slider";

class Features extends Component {
  state = {};
  render() {
    return (
      <div className='features'>
        <div className='text-center position-relative fe-title'>
          <img className='fe-bg' src={require("../../assets/images/features-bg.png")} alt='' />
          <h4>Features</h4>
        </div>
        <div className="fe-slider">
          <img className="img-badge" src={require('../../assets/images/slide-badge.png')} alt=""/>
          <SimpleSlider />
        </div>
      </div>
    );
  }
}

export default Features;
