import React, { Component } from "react";
import "./unbind.css";
class Unbind extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className='row  unbind'>
          <div className='col-md-6 fir'>
            {/* left side  */}
            <picture>
              <source media="(max-width: 567.98px)" srcSet={require("../../assets/images/mobile-unbind.png")}/>
            <img className="gogo" src={require("../../assets/images/unbined.png")} alt='' />
            </picture>
          </div>
          <div className='col-md-6 las d-flex justify-content-center'>
            <div className='text-center '>
              <div className='easy-dash dash-unbind'>
                <h4>
                  Deactivate &amp; Unbind your license all in the same place with a click of a
                  button
                </h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Unbind;
